/* css for the tabs starts here */

  
.bloc-tabs {
    display: flex;
    margin-left: 20vw;
}
.tabs {
    padding: 15px;
    text-align: center;
    width: 30%;
    background: var(--backgroundColor);
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.274);
    box-sizing: content-box;
    position: relative;
    outline: none;
    transition: 0.4s all ease;
    font-family: var(--textFamily);
    color: var(--blackColor);
    font-size: 17px;
    text-transform: uppercase;
    font-weight: bold;
}
.tabs:not(:last-child){
    border-right: 1px solid rgba(0, 0, 0, 0.274);
}
.active-tabs  {
background: var(--primaryColor);
border-bottom: 1px solid transparent;
color:#fff;
}
.active-tabs::before {
content: "";
display: block;
position: absolute;
top: -5px;
left: 50%;
-webkit-transform: translateX(-50%);
        transform: translateX(-50%);
width: calc(100% + 2px);
height: 5px;
background: var(--mainBlue);
}

.content-tabs {
/* flex-grow : 1; */
}
.content {
width: 100%;
display: none;
}

.active-content {
display: block;
}

/* css code for the pictures */
.media-content{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(255px, 1fr));
    grid-row-gap:2rem;
    grid-column-gap: 38px;
    background: red;
    padding-bottom: 50px;
}
.mediaimg{
    width:100%;
    margin:0 auto;
    position: relative;
    overflow:hidden;
    background: transparent;
    padding:10px 10px 0px 10px;
}
.media-img img{
    border-radius: 10px;
    width: 100%;
    height: 350px;
}
.mediaimg:hover .layer-className{
    top:0;
}
.mediaimg .layer-className{
    position: absolute;
    top: calc(100% - 3px);
    left:0;
    width: 100%;
    height: 360px;
    background: linear-gradient(180deg, rgba(81, 216, 27, 0.443), rgba(129, 216, 58, 0.443));
    z-index:1;
    transition:0.5s
}
.media-title{
    position:absolute;
    bottom:0px;
    padding: 20px 10px;
    background: linear-gradient(180deg, rgba(4, 5, 3, 0.443), rgba(0, 0, 0, 0.443));
    width: 100%;
    text-align: center;
    font-size: 20px;
    font-family: var(--textFamily);
    color:#fff;
    text-transform: capitalize;
}

/* css for modal container */
.modal-container{
    background: red;
    width: 80%;
    height: 500px;
    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%)
}

